//gutenberg editor marker
#start-resizable-editor-section {
	display: none;
}

.wp-block-eedee-block-gutenslider {
	.eedee-gutenslider-pagination {
	  position: absolute;
	  text-align: center;
	  transition: 300ms opacity;
	  transform: translate3d(0, 0, 0);
	  z-index: 10;
		box-sizing: border-box;

	  &.swiper-pagination-hidden {
	    opacity: 0;
	  }

		&.swiper-pagination-bullets:not(.swiper-pagination-bullets-dynamic) {
			padding: 0 15px;
		}
	}

	&.dots-sm .eedee-gutenslider-pagination {
		display: block;
	}

	&.dots-sm-none .eedee-gutenslider-pagination {
		display: none;
	}
	/* Common Styles */
	.swiper-pagination-fraction,
	.swiper-pagination-custom,
	&.dot-position-top .swiper-vertical ~ .swiper-pagination-bullets,
	&.dot-position-bottom .swiper-vertical ~ .swiper-pagination-bullets,
	.swiper-horizontal ~ .swiper-pagination-bullets {
		justify-self: center;
	}
	/* Bullets */
	.swiper-pagination-bullets-dynamic {
		overflow: hidden;
		font-size: 0;
		.swiper-pagination-bullet {
			transform: scale(0.33);
			position: relative;
		}
		.swiper-pagination-bullet-active {
			transform: scale(1);
		}
		.swiper-pagination-bullet-active-main {
			transform: scale(1);
		}
		.swiper-pagination-bullet-active-prev {
			transform: scale(0.66);
		}
		.swiper-pagination-bullet-active-prev-prev {
			transform: scale(0.33);
		}
		.swiper-pagination-bullet-active-next {
			transform: scale(0.66);
		}
		.swiper-pagination-bullet-active-next-next {
			transform: scale(0.33);
		}
	}
	.swiper-pagination-clickable .swiper-pagination-bullet {
		cursor: pointer;
	}

	.swiper-pagination-bullet {
		width: calc(var(--gutenslider-dot-size-sm) - 14px);
		height: calc(var(--gutenslider-dot-size-sm) - 14px);
		display: inline-block;
		border-radius: 50%;
		background: var(--gutenslider-dot-color);
		opacity: 0.4;
		@at-root button#{&} {
			border: none;
			margin: 0;
			padding: 0;
			box-shadow: none;
			appearance: none;
		}
	}
	.swiper-pagination-bullet-active {
		opacity: 1;
	}

	&:not(.dot-position-top):not(.dot-position-bottom) {
		.swiper-vertical {
			~ .swiper-pagination-bullets {
				top: 50%;
				transform: translate3d(0px, -50%, 0);
				.swiper-pagination-bullet {
					margin: 6px 0;
					display: block;
				}
				&.swiper-pagination-bullets-dynamic {
					top: 50%;
					transform: translateY(-50%);
					width: 8px;
					.swiper-pagination-bullet {
						display: inline-block;
						transition: 200ms transform, 200ms top;
					}
				}
			}
		}
	}
	&.dot-position-top .swiper-vertical,
	&.dot-position-bottom .swiper-vertical,
	.swiper-horizontal {
		~ .swiper-pagination-bullets {
			.swiper-pagination-bullet {
				margin: 0 4px;
			}
			&.swiper-pagination-bullets-dynamic {
				left: 50%;
				transform: translateX(-50%);
				white-space: nowrap;
				.swiper-pagination-bullet {
					transition: 200ms transform, 200ms left;
				}
			}
		}
		&.swiper-rtl ~ .swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
			transition: 200ms transform, 200ms right;
		}
	}
	/* Fraction */
	.swiper-pagination-fraction,
	.swiper-pagination-custom {
		color: var(--gutenslider-dot-color);
		font-size: var(--gutenslider-dot-size-sm);
		line-height: var(--gutenslider-dot-size-sm);
		width: auto;
		height: var(--gutenslider-dot-size-sm);
	}
	/* Progress */
	.swiper-pagination-progressbar {
		background: rgba(0, 0, 0, 0.25);
		position: absolute;
		.swiper-pagination-progressbar-fill {
			background: var(--gutenslider-dot-color);
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			transform: scale(0);
			transform-origin: left top;
		}
		.swiper-rtl & .swiper-pagination-progressbar-fill {
			transform-origin: right top;
		}
	}
	.swiper-pagination-lock {
		display: none;
	}

	&.dot-position-top {
		.eedee-gutenslider-pagination {
			top: var(--gutenslider-dot-y-offset-sm);
		}

		.swiper-pagination-progressbar{
			width: 100%;
			height: var(--gutenslider-dot-size-sm);
			left: 0;
		}
	}

	&.dot-position-bottom {
		.eedee-gutenslider-pagination {
			bottom: var(--gutenslider-dot-y-offset-sm);
		}

		.swiper-pagination-progressbar{
			width: 100%;
			height: var(--gutenslider-dot-size-sm);
			left: 0;
		}
	}

	&.dot-position-right {
		.swiper-pagination-progressbar {
			height: 100%;
			width: var(--gutenslider-dot-size-sm);
			left: 0;
		}

		.eedee-gutenslider-pagination {
			right: var(--gutenslider-dot-y-offset-sm);
			top: 50%;
			transform: translate3d(0px, -50%, 0);
			left: unset;

			.swiper-pagination-bullet {
			  margin: 6px 0;
			  display: block;
			}

			&.swiper-pagination-bullets-dynamic {
				top: 50%;
				transform: translateY(-50%);
				width: 8px;

				.swiper-pagination-bullet {
					// display: inline-block;
					transition: 200ms transform, 200ms top;
					left: 0 !important;
				}
			}
		}
	}

	&.dot-position-left {
		.swiper-pagination-progressbar {
			height: 100%;
			width: var(--gutenslider-dot-size-sm);
			left: 0;
		}

		.eedee-gutenslider-pagination {
			left: var(--gutenslider-dot-y-offset-sm);
			top: 50%;
			transform: translate3d(0px, -50%, 0);
			right: unset;

			.swiper-pagination-bullet {
			  margin: 6px 0;
			  display: block;
			}

			&.swiper-pagination-bullets-dynamic {
				top: 50%;
				transform: translateY(-50%);
				width: 8px;

				.swiper-pagination-bullet {
					// display: inline-block;
					left: 0 !important;
					transition: 200ms transform, 200ms top;
				}
			}
		}
	}

	&.dot-position-top-left .swiper-pagination-fraction {
		top: var(--gutenslider-dot-y-offset-sm);
		left: 10px;
		right: unset;
		width: unset !important;
	}

	&.dot-position-top-right .swiper-pagination-fraction {
		top: var(--gutenslider-dot-y-offset-sm);
		right: 10px;
		left: unset;
		width: unset !important;
	}

	&.dot-position-bottom-left .swiper-pagination-fraction {
		bottom: var(--gutenslider-dot-y-offset-sm);
		left: 10px;
		right: unset;
		width: unset !important;
	}

	&.dot-position-bottom-right .swiper-pagination-fraction {
		bottom: var(--gutenslider-dot-y-offset-sm);
		right: 10px;
		left: unset;
		width: unset !important;
	}

	@media only screen and (min-width: 961px) {
		&.dots-lg .eedee-gutenslider-pagination {
			display: block;
		}

		&.dots-lg-none .eedee-gutenslider-pagination {
			display: none;
		}

		.swiper-pagination-progressbar {
			height: var(--gutenslider-dot-size);
		}

		.swiper-pagination-fraction {
			font-size: var(--gutenslider-dot-size);
			line-height: var(--gutenslider-dot-size);
			height: var(--gutenslider-dot-size);
		}

		.swiper-pagination-bullet {
			width: calc(var(--gutenslider-dot-size) - 14px);
			height: calc(var(--gutenslider-dot-size) - 14px);
		}

		&.dot-position-bottom {
			.eedee-gutenslider-pagination {
				bottom: var(--gutenslider-dot-y-offset);
			}

			.swiper-pagination-progressbar{
				height: var(--gutenslider-dot-size);
			}
		}

		&.dot-position-top {
			.eedee-gutenslider-pagination {
				top: var(--gutenslider-dot-y-offset);
			}

			.swiper-pagination-progressbar{
				height: var(--gutenslider-dot-size);
			}
		}

		&.dot-position-right {
			.swiper-pagination-progressbar {
				width: var(--gutenslider-dot-size);
			}
			.eedee-gutenslider-pagination {
				right: var(--gutenslider-dot-y-offset);
			}
		}

		&.dot-position-left {
			.swiper-pagination-progressbar {
				width: var(--gutenslider-dot-size);
			}
			.eedee-gutenslider-pagination {
				left: var(--gutenslider-dot-y-offset);
			}
		}

		&.dot-position-top-left .swiper-pagination-fraction {
			top: var(--gutenslider-dot-y-offset);
		}

		&.dot-position-top-right .swiper-pagination-fraction {
			top: var(--gutenslider-dot-y-offset);
		}

		&.dot-position-bottom-left .swiper-pagination-fraction {
			bottom: var(--gutenslider-dot-y-offset);
		}

		&.dot-position-bottom-right .swiper-pagination-fraction {
			bottom: var(--gutenslider-dot-y-offset);
		}
	}

	@media only screen and (min-width: 601px) and (max-width: 960px) {
		&.dots-md .eedee-gutenslider-pagination {
			display: block;
		}

		&.dots-md-none .eedee-gutenslider-pagination {
			display: none;
		}

		.swiper-pagination-progressbar {
			height: var(--gutenslider-dot-size-md);
		}

		.swiper-pagination-fraction {
			font-size: var(--gutenslider-dot-size-md);
			line-height: var(--gutenslider-dot-size-md);
			height: var(--gutenslider-dot-size-md);
		}

		.swiper-pagination-bullet {
			width: calc(var(--gutenslider-dot-size-md) - 14px);
			height: calc(var(--gutenslider-dot-size-md) - 14px);
		}

		&.dot-position-bottom {
			.eedee-gutenslider-pagination {
				bottom: var(--gutenslider-dot-y-offset);
			}

			.swiper-pagination-progressbar{
				height: var(--gutenslider-dot-size-md);
			}
		}

		&.dot-position-top {
			.eedee-gutenslider-pagination {
				top: var(--gutenslider-dot-y-offset-md);
			}

			.swiper-pagination-progressbar{
				height: var(--gutenslider-dot-size-md);
			}
		}

		&.dot-position-right {
			.swiper-pagination-progressbar {
				width: var(--gutenslider-dot-size-md);
			}
			.eedee-gutenslider-pagination {
				right: var(--gutenslider-dot-y-offset-md);
			}
		}

		&.dot-position-left {
			.swiper-pagination-progressbar {
				width: var(--gutenslider-dot-size-md);
			}
			.eedee-gutenslider-pagination {
				left: var(--gutenslider-dot-y-offset-md);
			}
		}

		&.dot-position-top-left .swiper-pagination-fraction {
			top: var(--gutenslider-dot-y-offset-md);
		}

		&.dot-position-top-right .swiper-pagination-fraction {
			top: var(--gutenslider-dot-y-offset-md);
		}

		&.dot-position-bottom-left .swiper-pagination-fraction {
			bottom: var(--gutenslider-dot-y-offset-md);
		}

		&.dot-position-bottom-right .swiper-pagination-fraction {
			bottom: var(--gutenslider-dot-y-offset-md);
		}
	}
}

